class Api {
    constructor(options) {
        this._url = options.url;
        this._headers = options.headers;
    }

    _getResponseData(res) {
        if(res.ok) {
            return res.json();
        }
        return Promise.reject(new Error(`–û—à–∏–±–∫–∞: ${res.status}`));
    }

    //–ù–∞ —Å–ª—É—á–∞–π, –µ—Å–ª–∏ –µ—Å—Ç—å –Ω–µ—Å–∫–æ–ª—å–∫–æ GET –∑–∞–ø—Ä–æ—Å–æ–≤ —Å —Ä–∞–∑–Ω—ã–º–∏ –¥–∞–Ω–Ω—ã–º–∏
    getData() {
        return fetch(`${this._url}`, {
            headers: this._headers,
        });
    }

    //GET –∑–∞–ø—Ä–æ—Å –Ω–∞ –º–∞—Å—Å–∏–≤ —Å —Ç—Ä–µ–∫–∞–º–∏
    getTracksData() {
        return this.getData()
           .then(res => this._getResponseData(res));  
    }

    //POST –∑–∞–ø—Ä–æ—Å –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
    submitForm(formData) {
        return fetch(`${this._url}`, {
            method: "POST",
            headers:  this._headers,
            body: JSON.stringify({
                name: formData.name,
                email: formData.email,
                tel: formData.tel,
                text: formData.text,
                offer: formData.offer
              })
        }).then(res => this._getResponseData(res));
       
    }
}

//–°—é–¥–∞ –ø–æ–¥—Å—Ç–∞–≤–ª—è–µ—Ç—Å—è –ª—é–±–æ–π URL –∏ –∑–∞–≥–æ–ª–æ–≤–∫–∏ –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏ –¥–∞–Ω–Ω—ã—Ö —Ñ–æ—Ä–º—ã. –û—Ç–ø—Ä–∞–≤–∫–∞ –ø—Ä–æ—Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∞ –∏ —Å—Ç–∞–±–∏–ª—å–Ω–∞.
