import './StreamServices.css';

import { useEffect, useState } from 'react';
import { useTrail, animated, Transition } from 'react-spring';
import cn from 'classnames';

import { serviceLinks } from '../configs/links';

import StreamServiceLink from './StreamServiceLink';
import LinksCloseIcon from './svg/LinksCloseIcon';

/* TODO - –Ω–∞–ø–∏—Å–∞—Ç—å –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π –∫ –∞–Ω–∏–º–∞—Ü–∏–∏ –∫–Ω–æ–ø–∫–∏ –°—Ç—Ä–∏–º–∏–Ω–≥–∏ */
/* TODO - –≤—ã–Ω–µ—Å—Ç–∏ –º–∞–≥–∏—á–µ—Å–∫–∏–µ —á–∏—Å–ª–∞ –∏–∑ –ø—Ä–æ–ø–µ—Ä—Ç–∏ config –≤ –Ω–æ—Ä–º–∞–ª—å–Ω—ã–µ –ø–µ—Ä–µ–º–µ–Ω–Ω—ã–µ (–¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏) */

function StreamServices({ className, isMobile, isLandscape, isPlayerExtend }) {
  /* –†–∞–∑–¥–µ–ª–µ–Ω–∏–µ –º–µ–∂–¥—É —Å—Ç–µ–π—Ç–∞–º–∏ isLinksMounted –∏ isLinksHidden —Å–¥–µ–ª–∞–Ω–æ –¥–ª—è –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–π –∞–Ω–∏–º–∞—Ü–∏–∏.
  –°—Å—ã–ª–∫–∏ –¥–æ–ª–∂–Ω—ã —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞—Ç—å—Å—è –ø–æ –æ–∫–æ–Ω—á–∞–Ω–∏–∏ –∞–Ω–∏–º–∞—Ü–∏–∏, –∏–Ω–∞—á–µ –∞–Ω–∏–º–∞—Ü–∏–∏ –Ω–µ –±—É–¥–µ—Ç. */
  const [isLinksMounted, setIsLinksMounted] = useState(false);
  const [isLinksHidden, setIsLinksHidden] = useState(true);

  const handleServiceButtonClick = () => {
    setIsLinksHidden(!isLinksHidden);
    setIsLinksMounted(true);
  };

  /* –°–æ–∑–¥–∞–µ–º –∞–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—É—é –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—É –∏–∑ –æ–±—ã—á–Ω–æ–π */
  const AnimatedStreamServiceLink = animated(StreamServiceLink);

  /* –ö–æ–Ω—Ñ–∏–≥ –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ —Å—Å—ã–ª–æ–∫ –Ω–∞ —à–∏—Ä–æ–∫–æ–º —ç–∫—Ä–∞–Ω–µ. 
  –°–µ–π—á–∞—Å –∫–æ–Ω—Ñ–∏–≥ –æ–±–µ—Å–ø–µ—á–∏–≤–∞–µ—Ç —Å—Ç–∞—Ç–∏—á–Ω–æ–µ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã */
  const desktopConfig = {
    opacity: 1,
    x: 0
  };

  /* –ö–æ–Ω—Ñ–∏–≥ –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ —Å—Å—ã–ª–æ–∫ –Ω–∞ —É–∑–∫–∏—Ö —ç–∫—Ä–∞–Ω–∞—Ö. 
  –ú–µ–Ω—è—é—Ç—Å—è –ø—Ä–æ–∑—Ä–∞—á–Ω–æ—Å—Ç—å –∏ —Å–º–µ—â–µ–Ω–∏–µ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏*/
  const mobileConfig = {
    config: { mass: 1, tension: 200, friction: 18 },
    to: {
      opacity: !isLinksHidden ? 1 : 0,
      x: !isLinksHidden ? 0 : -50
    },
    from: {
      opacity: 0,
      x: -50
    },
    reverse: isLinksHidden,
    onRest: () => {
      if (isLinksHidden) {
        setIsLinksMounted(false);
      }
    }
  };

  /* –ú–∞—Å—Å–∏–≤ —Å –æ–±—ä–µ–∫—Ç–∞–º–∏ –ø—Ä–æ–ø—Å–æ–≤ –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ –∫–∞–∂–¥–æ–π —Å—Å—ã–ª–∫–∏ */
  const trail = useTrail(serviceLinks.length, (isMobile || isLandscape) ? mobileConfig : desktopConfig);

  /* –°–±–æ—Ä–∫–∞ –æ–±—ä–µ–∫—Ç–æ–≤ —Å –ø–æ–ª–Ω—ã–º –Ω–∞–±–æ—Ä–æ–º –ø—Ä–æ–ø—Å–æ–≤ –¥–ª—è –±—É–¥—É—â–∏—Ö –∞–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç.
  –û–±—ä–µ–¥–∏–Ω—è—é—Ç—Å—è –ø—Ä–æ–ø—Å—ã –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ –∏ –æ–±—ã—á–Ω—ã–µ –ø—Ä–æ–ø—Å—ã (–Ω–∞–∑–≤–∞–Ω–∏–µ –∏ –∞–¥—Ä–µ—Å —Å—Å—ã–ª–∫–∏).
  –û–±—ä–µ–∫—Ç–æ–≤ –≤ –º–∞—Å—Å–∏–≤–µ —Å—Ç–æ–ª—å–∫–æ, —Å–∫–æ–ª—å–∫–æ —Å—Å—ã–ª–æ–∫ –Ω–∞ —Å—Ç—Ä–∏–º–∏–Ω–≥–æ–≤—ã–µ —Å–µ—Ä–≤–∏—Å—ã. */
  const serviceLinksProps = serviceLinks.map((link, i) => {
    const linkProps = {};
    Object.assign(linkProps, link, trail[i]);
    return linkProps;
  });

  /* –î–ª—è –º–∞–ª–µ–Ω—å–∫–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤ –∏ landscape: –µ—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Ä–∞–∑–≤–µ—Ä–Ω—É–ª —Å—Å—ã–ª–∫–∏ –∏ –æ—Ç–∫—Ä—ã–ª –ø–ª–µ–µ—Ä, 
  —Ç–æ —Å–≤–æ—Ä–∞—á–∏–≤–∞–µ–º —Å—Å—ã–ª–∫–∏, —á—Ç–æ–±—ã –¥–∞—Ç—å –ø–ª–µ–µ—Ä—É –ø–æ–ª–Ω–æ—Å—Ç—å—é –≤—ã–µ—Ö–∞—Ç—å*/
  useEffect(() => {
    if (isPlayerExtend && (isMobile || isLandscape)) {
      setIsLinksHidden(true);
      setIsLinksMounted(false);
    }
  }, [isPlayerExtend, isLandscape, isMobile]);

  return (
    <div className={cn("stream-services", className)}>
      {(isMobile || isLandscape) && (
        <button
          className={cn("stream-services__button", {
            "stream-services__button_minimised": !isLinksHidden,
          })}
          type="button"
          onClick={handleServiceButtonClick}
        >
          <Transition
            items={isLinksHidden ? "–°—Ç—Ä–∏–º–∏–Ω–≥–∏" : <LinksCloseIcon />}
            from={{ opacity: 0 }}
            enter={{ opacity: 1 }}
            initial={{ opacity: 1 }}
            config={{ mass: 1, tension: 140, friction: 30 }}
            keys={() => (isLinksHidden ? 1 : 2)} // —É–±–∏—Ä–∞–µ–º –ª–∏—à–Ω–∏–µ —Ä–µ–Ω–¥–µ—Ä—ã –¥–ª—è animated.span
          >
            {(values, item) => (
              <animated.span style={values}>{item}</animated.span>
            )}
          </Transition>
        </button>
      )}

      {(isLinksMounted || !(isMobile || isLandscape)) && (
        <ul className="stream-services__links">
          {serviceLinksProps.map((item, i) => (
            <AnimatedStreamServiceLink
              key={i}
              serviceLabel={item.serviceLabel}
              serviceLink={item.serviceLink}
              style={{
                ...item,
              }}
            />
          ))}
        </ul>
      )}
    </div>
  );
}

export default StreamServices;
