import { useState, useEffect } from 'react';

/*
–•—É–∫ useAudioPlayer –¥–ª—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è –∞—É–¥–∏–æ–ø–ª–µ–µ—Ä–∞.
audioElementId -- (—Å—Ç—Ä–æ–∫–∞, –Ω–∞–ø—Ä–∏–º–µ—Ä 'audio')
track -- —Ç—Ä–µ–∫ –∏–∑ —Å–ø–∏—Å–∫–∞ –ø–µ—Å–µ–Ω, —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç—Å—è –≤ –∫–∞—á–µ—Å—Ç–≤–µ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ 
        (useEffect –ø—Ä–∏ —Å–º–µ–Ω–µ —Ç—Ä–µ–∫–∞)
*/
function useAudioPlayer(audioPlayerRef, track) {
  const [isLoaded, setLoadedState] = useState(false);

  const [duration, setDuration] = useState(0);
  const [curTime, setCurTime] = useState(0);
  const [isPlaying, setPlaying] = useState(false);
  const [clickedTime, setClickedTime] = useState();

  const handlePlayClick = () => {
    setPlaying(!isPlaying);
    isPlaying ? audioPlayerRef.current.pause() : audioPlayerRef.current.play();
  }

  const handleLoadedMetaData = () => {
    setDuration(audioPlayerRef.current.duration);
    setCurTime(audioPlayerRef.current.currentTime);
    setLoadedState(true);
  }

  const handleTimeUpdate = () => setCurTime(audioPlayerRef.current.currentTime);

  const handleTrackEnded = () => {
    setDuration(audioPlayerRef.current.duration);
    setCurTime(0);
    handlePlayClick();
  }
  
  useEffect(() => {
    if (clickedTime && clickedTime !== curTime) {
      audioPlayerRef.current.currentTime = clickedTime;
      setClickedTime(null);
    } 
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [clickedTime]);

  useEffect(() => {
    setCurTime(0);
    setPlaying(false);
    setLoadedState(false);
  }, [track]);

  return {
    isPlaying,
    handlePlayClick,
    isLoaded,
    handleTimeUpdate,
    handleLoadedMetaData,
    setClickedTime,
    handleTrackEnded,
    curTime,
    duration
  }
}

export default useAudioPlayer;
