//–ö–∞—Å—Ç–æ–º–∏–∑–∞—Ü–∏—è –æ—à–∏–±–æ–∫ –¥–ª—è –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —Ñ–æ—Ä–º—ã
export default function setCustomValidity (target) {
        target.setCustomValidity('');

        if (!target.validity.valid){
             if (target.validity.valueMissing) {
                target.setCustomValidity('–≠—Ç–æ –ø–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ');
            } else if (target.name === 'name' & target.validity.patternMismatch) {
                target.setCustomValidity('–ò–º—è –∏ —Ñ–∞–º–∏–ª–∏—è –º–æ–≥—É—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ç–æ–ª—å–∫–æ –±—É–∫–≤—ã')
            }  else if (target.name === 'tel' & target.validity.patternMismatch) {
                target.setCustomValidity('–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ç–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä—ã –∏ —Å–∏–º–≤–æ–ª—ã "+", "-"')
            } else if (target.validity.tooShort) {
                target.setCustomValidity(`–í–≤–µ–¥–µ–Ω–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –¥–ª–∏–Ω–µ–µ ${target.minLength} —Å–∏–º–≤–æ–ª–æ–≤`);
            } else if (target.validity.tooLong) {
                target.setCustomValidity(`–í–≤–µ–¥–µ–Ω–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –∫–æ—Ä–æ—á–µ ${target.maxLength} —Å–∏–º–≤–æ–ª–æ–≤`)
            } else if (target.name === 'email' & target.validity.patternMismatch) {
                target.setCustomValidity('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –Ω–µ –∑–∞–±—É–¥—å—Ç–µ "@" –∏ –¥–æ–º–µ–Ω')
            }
        } 
    }

  