function throttle(func, ms) {
  
  let isThrottled = false,
    savedArgs,
    savedThis;

  const wrapper = () => {

    if (isThrottled) {
      // –∑–∞–ø–æ–º–∏–Ω–∞–µ–º –ø–æ—Å–ª–µ–¥–Ω–∏–µ –∞—Ä–≥—É–º–µ–Ω—Ç—ã –¥–ª—è –≤—ã–∑–æ–≤–∞ –ø–æ—Å–ª–µ –∑–∞–¥–µ—Ä–∂–∫–∏
      // eslint-disable-next-line no-undef
      savedArgs = arguments;
      savedThis = this;
      return;
    }

    // –≤ –ø—Ä–æ—Ç–∏–≤–Ω–æ–º —Å–ª—É—á–∞–µ –ø–µ—Ä–µ—Ö–æ–¥–∏–º –≤ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∑–∞–¥–µ—Ä–∂–∫–∏
    // eslint-disable-next-line no-undef
    func.apply(this, arguments);

    isThrottled = true;

    // –Ω–∞—Å—Ç—Ä–æ–π–∫–∞ —Å–±—Ä–æ—Å–∞ isThrottled –ø–æ—Å–ª–µ –∑–∞–¥–µ—Ä–∂–∫–∏
    setTimeout(() => {
      isThrottled = false;
      if (savedArgs) {
        // –µ—Å–ª–∏ –±—ã–ª–∏ –≤—ã–∑–æ–≤—ã, savedThis/savedArgs —Ö—Ä–∞–Ω—è—Ç –ø–æ—Å–ª–µ–¥–Ω–∏–π –∏–∑ –Ω–∏—Ö
        // —Ä–µ–∫—É—Ä—Å–∏–≤–Ω—ã–π –≤—ã–∑–æ–≤ –∑–∞–ø—É—Å–∫–∞–µ—Ç —Ñ—É–Ω–∫—Ü–∏—é –∏ —Å–Ω–æ–≤–∞ —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç –≤—Ä–µ–º—è –∑–∞–¥–µ—Ä–∂–∫–∏
        wrapper.apply(savedThis, savedArgs);
        savedArgs = savedThis = null;
      }
    }, ms);
  }

  return wrapper;
}

export default throttle;
