import { useState, useEffect } from 'react';
import throttle from '../utils/throttle';

/*
–•—É–∫ useTicker —Ä–µ–∞–ª–∏–∑—É–µ—Ç –±–µ–≥—É—â—É—é —Å—Ç—Ä–æ–∫—É.
–ò–∑–º–µ—Ä—è–µ—Ç —Ç–µ–∫—É—â—É—é —à–∏—Ä–∏–Ω—É —ç–ª–µ–º–µ–Ω—Ç–∞ –∏ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞, –æ–ø—Ä–µ–¥–µ–ª—è–µ—Ç –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç—å
–ø–æ—è–≤–ª–µ–Ω–∏—è –±–µ–≥—É—â–µ–π —Å—Ç—Ä–æ–∫–∏ –∏ –∑–∞–ø—É—Å–∫–∞–µ—Ç –µ—ë.
–†–µ–∞–≥–∏—Ä—É–µ—Ç –Ω–∞ 'resize' –æ–∫–Ω–∞ –±—Ä–∞—É–∑–µ—Ä–∞.
elementRef -- —Å—Å—ã–ª–∫–∞ –Ω–∞ —ç–ª–µ–º–µ–Ω—Ç, –∫–æ—Ç–æ—Ä—ã–π —Å—Ç–∞–Ω–æ–≤–∏—Ç—Å—è –±–µ–≥—É—â–µ–π —Å—Ç—Ä–æ–∫–æ–π.
containerTickerAddClass -- –∫–ª–∞—Å—Å, –¥–æ–±–∞–≤–ª—è–µ–º—ã–π –≤ —Å–ø–∏—Å–æ–∫ –∫–ª–∞—Å—Å–æ–≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞
      –ø—Ä–∏ –ø–æ—è–≤–ª–µ–Ω–∏–∏ –≤ –Ω—ë–º –±–µ–≥—É—â–µ–π —Å—Ç—Ä–æ–∫–∏.
dependences -- –º–∞—Å—Å–∏–≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–µ–π, –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –∫–æ—Ç–æ—Ä—ã—Ö —Å–Ω–æ–≤–∞ –ø—Ä–æ–≤–µ—Ä—è–µ—Ç—Å—è 
      –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç—å –ø–æ—è–≤–ª–µ–Ω–∏—è –±–µ–≥—É—â–µ–π —Å—Ç—Ä–æ–∫–∏.
*/
function useTicker({ elementRef, containerTickerAddClass, dependences }) {
  // —Å—Ç–µ–π—Ç-–ø–µ—Ä–µ–º–µ–Ω–Ω–∞—è, –æ–ø—Ä–µ–¥–µ–ª—è—é—â–∞—è –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç—å –±–µ–≥—É—â–µ–π —Å—Ç—Ä–æ–∫–∏
  const [isTickerNeeded, setTickerState] = useState(false);
  
  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Ä–µ—Å–∞–π–∑–∞ –æ–∫–Ω–∞
  const handleResize = () => {
    const element = elementRef.current;
    const elementContainer = element.parentElement;
    const elementWidth = element.scrollWidth;
    const elementContainerWidth = elementContainer.clientWidth;
    if (elementWidth > elementContainerWidth) {
      setTickerState(true);
    } else {
      setTickerState(false);
    }
  }
  // TODO -- –º–æ–∂–Ω–æ –ö–ê–ö-–¢–û –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –¥–ª—è –ø–∞—É–∑—ã –≤ –±–µ–≥—É—â–µ–π —Å—Ç—Ä–æ–∫–µ
  // –§—É–Ω–∫—Ü–∏—è –∑–∞–¥–µ—Ä–∂–∫–∏
  const delay = async (ms) => await new Promise(resolve => setTimeout(resolve, ms));
  
  // –ü—Ä–æ–≤–µ—Ä—è–µ–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç—å –∑–∞–ø—É—Å–∫–∞ –±–µ–≥—É—â–µ–π —Å—Ç—Ä–æ–∫–∏ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã,
  // –ø—Ä–µ–¥–≤–∞—Ä–∏—Ç–µ–ª—å–Ω–æ —Å–±—Ä–∞—Å—ã–≤–∞—è isTickerNeeded –Ω–∞ false –¥–ª—è –≤—ã—Å—Ç–∞–≤–ª–µ–Ω–∏—è "–±–µ–≥—É—â–µ–≥–æ"
  // —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ –∏—Å—Ö–æ–¥–Ω–æ–µ –ø–æ–ª–æ–∂–µ–Ω–∏–µ.
  useEffect(() => {
    async function checkIsTickerNeeded() {
      await delay(2000);
      handleResize();
    };
    setTickerState(false);
    checkIsTickerNeeded();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, dependences);

  // –ó–∞—Ç–æ—Ä–º–∞–∂–∏–≤–∞–µ–º –æ–±—Ä–∞–±–æ—Ç–∫—É —Ä–µ—Å–∞–π–∑–∞ –æ–∫–Ω–∞ –±—Ä–∞—É–∑–µ—Ä–∞
  const handleResizeThrottled = throttle(handleResize, 1000);

  // –î–æ–±–∞–≤–ª—è–µ–º —Å–ª—ã—à–∞—Ç–µ–ª–∏ –Ω–∞ —Ä–µ—Å–∞–π–∑ –æ–∫–Ω–∞
  useEffect(() => {
    window.addEventListener('resize', handleResizeThrottled);
    return () => {
      window.removeEventListener('resize', handleResizeThrottled);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  
  // –î–æ–±–∞–≤–ª—è–µ–º —Å—Ç–µ–π—Ç –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è —Ç–µ–∫—É—â–µ–≥–æ –Ω–æ–º–µ—Ä–∞ —Ç–∞–π–º–µ—Ä–∞
  const [intervalId, setIntervalId] = useState(null);
  
  // –ü—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ isTickerNeeded –∑–∞–ø—É—Å–∫–∞–µ–º –∏–ª–∏ –æ—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –±–µ–≥—É—â—É—é —Å—Ç—Ä–æ–∫—É
  useEffect(() => {
    const element = elementRef.current;
    const elementContainer = element.parentElement;
    const elementWidth = element.scrollWidth;
    const elementContainerWidth = elementContainer.clientWidth;
  
    const startTicker = () => {
      // TODO -- –ø–∞—É–∑–∞
      currentX--;
      element.style.left = `${currentX}px`;
      if (currentX === -elementWidth) {
        // 10 –∑–¥–µ—Å—å —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç "–∑–∞–¥–µ—Ä–∂–∫—É" –≤ –ø–∏–∫—Å–µ–ª—è—Ö –ø–µ—Ä–µ–¥ –ø–æ—è–≤–ª–µ–Ω–∏–µ–º —Å—Ç—Ä–æ–∫–∏ —Å–ø—Ä–∞–≤–∞
        currentX = elementContainerWidth + 10;
      }
    }
  
    let currentX = 0;
    
    if (isTickerNeeded) {
      elementContainer.classList.add(containerTickerAddClass);
      const currentIntervalId = setInterval(startTicker, 13);
      setIntervalId(currentIntervalId)
    } else {
      elementContainer.classList.remove(containerTickerAddClass);
      clearInterval(intervalId);
      element.style.left = '0px';
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isTickerNeeded]);
}

export default useTicker;
