import React from 'react';
import './Form.css';
import pdfUrl from '../vendor/offer.pdf';
import useFormWithValidation from '../hooks/useFormWithValidation.js';
import setCustomValidity from '../utils/setCustomValidity.js';
import { api } from '../utils/Api.js';


function Form() {

    const { values, handleChange, errors, isFormValid, resetForm } = useFormWithValidation(setCustomValidity);
    const [isSubmitted, setIsSubmitted] = React.useState(false);
    const [isErrorVisible, setIsErrorVisible] = React.useState(false);

    function handleSubmit(evt) {
        evt.preventDefault();
        console.log(values);

        api.submitForm(values)
           .then(() => {
              setIsSubmitted(true);
              setIsErrorVisible(false);
              resetForm();

              setTimeout(() => 
                setIsSubmitted(false),
                5000);
           })
           .catch((err) => {
               console.log(err);
               setIsErrorVisible(true);
           })
    }

    return(
        <div className="form-container">
        <form className="form" name="send-poem" onSubmit={handleSubmit} noValidate>
                <h2 className="form__heading">–§–æ—Ä–º–∞</h2>
                <p className="form__text">–ó–∞–ø–æ–ª–Ω—è—è —ç—Ç—É —Ñ–æ—Ä–º—É, –≤—ã —Å—Ç–∞–Ω–æ–≤–∏—Ç–µ—Å—å —á–∞—Å—Ç—å—é –ø—Ä–æ–µ–∫—Ç–∞.</p>

                <input 
                  className={errors.name? 'form__input form__input_name form__input_invalid' : 'form__input form__input_name'}
                  name="name" 
                  placeholder="–ò–º—è –∏ —Ñ–∞–º–∏–ª–∏—è –∞–≤—Ç–æ—Ä–∞" 
                  required 
                  minLength="2" 
                  maxLength="50" 
                  pattern="^[A-Za-z–ê-–Ø–∞-—è—ë–Å\s\-]+$"
                  value={values.name || ''}
                  onChange={handleChange}
                />
                <span className="form__input-error" id="name-error">{errors.name || ''}</span>

                <input 
                  className={errors.tel? 'form__input form__input_tel form__input_invalid' : 'form__input form__input_tel'}
                  type="tel" 
                  name="tel" 
                  placeholder="–¢–µ–ª–µ—Ñ–æ–Ω" 
                  required
                  minLength="10"
                  maxLength="20"
                  pattern="^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$"
                  value={values.tel || ''}
                  onChange={handleChange}
                />
                <span className="form__input-error" id="phone-error">{errors.tel || ''}</span>

                <input 
                  className={errors.email? 'form__input form__input_email form__input_invalid' : 'form__input form__input_email'}
                  type="email" 
                  name="email" 
                  placeholder="–ü–æ—á—Ç–∞" 
                  required 
                  minLength="6" 
                  maxLength="50" 
                  pattern="^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5})$"
                  value={values.email || ''}
                  onChange={handleChange}
                />
                <span className="form__input-error" id="email-error">{errors.email || ''}</span>


                <textarea 
                  className={errors.text? 'form__textarea form__input form__input_invalid' : 'form__textarea form__input'}
                  name="text" 
                  minLength="20" 
                  placeholder="–°—Ç–∏—Ö–∏" 
                  required
                  value={values.text || ''}
                  onChange={handleChange}
                >
                </textarea>
                <span className="form__input-error" id="text-error">{errors.text || ''}</span>

                <label htmlFor="offer" className="form__input-label">
                  <input 
                    className="form__input_radio" 
                    type="checkbox" 
                    name="offer" 
                    value="agree" 
                    id="offer"
                    required 
                    checked={values.offer}
                    onChange={handleChange}
                  />
                  <span className="form__pseudo-item"></span>
        
                  <span className="form__label-text">–°–æ–≥–ª–∞—Å–µ–Ω —Å <a className="form__offer-link" target="_blank" href={pdfUrl} rel="noreferrer">–æ—Ñ–µ—Ä—Ç–æ–π</a></span>
                </label>
                <span className="form__input-error" id="offer-error">{errors.offer || ''}</span>


                <button type="submit" className="form__submit-button" disabled={!isFormValid}><span className="form__button-text">{isSubmitted? '–£—Ä–∞, —Ñ–æ—Ä–º–∞ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∞!' : '–û—Ç–ø—Ä–∞–≤–∏—Ç—å —Ñ–æ—Ä–º—É'}</span></button>
                <span className="form__fill-hint">{isFormValid? '' : '–ß—Ç–æ–±—ã –æ—Ç–ø—Ä–∞–≤–∏—Ç—å —Ñ–æ—Ä–º—É, –ø–æ–∂–∞–ª—É–π—Å—Ç–∞, –∑–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è'}</span>
                <span className="form__wrong-submit">{isErrorVisible? '–£–ø—Å, —á—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫ –∏ —Ñ–æ—Ä–º–∞ –Ω–µ –æ—Ç–ø—Ä–∞–≤–∏–ª–∞—Å—å, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑!' : ''}</span>
                
            </form>
      </div>
    )
}

export default Form;
